import {
  Box,
  Text,
  Accordion as GrommetAccordion,
  AccordionPanel,
  Anchor,
  DataTable,
} from "grommet";

import List from "./List";

const Accordion = ({ panels, columns }) => {
  const panelBody = (body) => (
    <Box pad={{vertical: "medium"}}>
      <Text>{body}</Text>
    </Box>
  );

  const panelTable = (table) => (
    <Box align="center" pad={{vertical: "medium"}}>
      <DataTable columns={columns} data={table} resizeable />
    </Box>
  );

  const panelLink = (link) => (
    <Box pad={{ vertical: "medium" }}>
      <Anchor
        href={link.url}
        target="_blank"
        label={link.label}
      />
    </Box>
  );

  return (
    <GrommetAccordion margin={{ vertical: "small" }}>
      {panels.map(({ header, body, list, table, link }) => (
        <AccordionPanel
          key={header}
          label={
            <Text
              margin={{ left: "xsmall", vertical: "small" }}
              size="large"
              weight={0}
            >
              {header}
            </Text>
          }
        >
          <Box pad={{ left: "xsmall", vertical: "medium" }}>
            {panelBody(body)}
            {list && List(list)}
            {table && panelTable(table)}
            {link && link.map((link) => panelLink(link))}
          </Box>
        </AccordionPanel>
      ))}
    </GrommetAccordion>
  );
};

export default Accordion;
